.hover-text { 
 visibility: hidden;
  width: 400px; 
  background-color: rgba(0, 0, 0, 0.8);
  color: #ffffff;
  text-align: center;
  border-radius: 5px;
  padding: 10px;
  position: absolute;
  z-index: 1;
  bottom: 100%; 
  left: 50%;
  transform: translateX(-50%); 
  opacity: 0;
  transition: visibility 0s, opacity 0.3s ease;
  font-family: "Funnel Display", sans-serif;
} 

.image-container:hover .hover-text {
  visibility: visible;
 opacity: 1;
}

